<template>
  <div>
    <!-- Computer -->
      <div v-if="!isMobile()">
          <HomeButton :size=small></HomeButton>
          <div class="aboutOpen-container">
              <div class="career-container" :key="career.id" v-for="career in careersData">
                  <router-link :to="{ name: 'Career', params: { careerName: career.job}}">
                      <CareerTitle :career=career></CareerTitle>
                  </router-link>
              </div>
              <div v-if="(about.selection == $route.params.aboutName)" :class="[(about.selection == $route.params.aboutName) ? 'about-open' : '']">
                  <router-link to="/">
                      <AboutHome :about=about></AboutHome>
                  </router-link>
                  <AboutExpanded :about=about></AboutExpanded>
                  <router-link :to="{ name: 'About', params: { aboutName: contact.selection}}">
                        <AboutHome :about=contact></AboutHome>
                  </router-link>
              </div>
              <div v-if="(contact.selection == $route.params.aboutName)" :class="[(contact.selection == $route.params.aboutName) ? 'contact-open' : '']">
                  <router-link :to="{ name: 'About', params: { aboutName: about.selection}}">
                      <AboutHome :about=about></AboutHome>
                  </router-link>
                  <router-link to="/">
                      <AboutHome :about=contact></AboutHome>
                  </router-link>
                  <transition name="slideU">
                      <ContactsList :contacts=contactsData @hover-contact="HoverContact" @unhover-contact="UnhoverContact"></ContactsList>
                  </transition>
                  
              </div>
          </div>
      </div>

      <!-- Mobile -->
        <div v-else>
          <div class="mobile-careerOpen-container">
            <MobileHeader></MobileHeader>
            <div class="mobile-about-open">
                <AboutHome v-if="($route.params.aboutName==about.selection)" :about=about></AboutHome>
                <AboutHome v-if="($route.params.aboutName==contact.selection)" :about=contact></AboutHome>
                <AboutExpanded v-if="($route.params.aboutName==about.selection)" :about=about></AboutExpanded>
                <ContactsList v-if="($route.params.aboutName==contact.selection)" :contacts=contactsData></ContactsList>
            </div>
          </div>
        </div>   
  </div>

</template>

<script>
import careersData from '../../data/careers.json'
import aboutsData from '../../data/abouts.json'
import contactsData from '../../data/contacts.json'

import CareerTitle from '../components/CareerTitle'
import AboutExpanded from '../components/AboutExpanded.vue'
import ContactsList from '../components/ContactsList.vue'
import AboutHome from '../components/AboutHome.vue'
import HomeButton from '../components/HomeButton.vue'
import MobileHeader from '../components/MobileHeader.vue'

export default {
    name: "AboutOpen",
    components: {
        CareerTitle,
        AboutExpanded,
        AboutHome,
        ContactsList,
        HomeButton,
        MobileHeader
    },
    props: {
        abouts: Array,
        contacts: Array
    },
    data () {
        return {
        careersData: careersData,
        contactsData: contactsData,
        aboutsData: aboutsData,
        about: aboutsData[0],
        contact: aboutsData[1],
        small: 'small'
        }
    },
    methods: {
        isMobile() {
            if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                return true
            } else {
                return false
            }
        },
        HoverContact(id) {
            this.contactsData.forEach(contact => {contact.hover=false})
            this.contactsData = this.contactsData.map((contact) => contact.id === id ? {...contact, hover: !contact.hover} : contact)
        },
        UnhoverContact(id) {
            this.contactsData = this.contactsData.map((contact) => contact.id === id ? {...contact, hover: !contact.hover} : contact)
        },
    }
}
</script>

