        grid-row: 2;
        grid-template-rows: 10% 80% 10%;
        place-self:  stretch;
        justify-self: center;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    .contact-open {
        grid-column: 1 / span 4;
        grid-row: 2;
        grid-template-rows: 10% 10% 80% ;
        place-self:  stretch;
        justify-self: center;
        flex-direction: column;
        justify-content: space-around;
    }
    .career-open {
        grid-row: span 1;
        grid-column: 1 /  span 4;
        place-self: center stretch;
        justify-self: center;
    }

// Mobile
.mobile-careerOpen-container {
    display: grid ;
    justify-content: center;
    grid-template-columns: 100vw;
    grid-template-rows: 10vh 10vh 80vh;
    height: 100vh;
    width: 100vw;
  }
  .mobile-header {
    display: inline-flex;
    height: 10vh;
    justify-content: space-evenly;
    align-items: center;
  }
  .mobile-career-image {
    max-height: 4vh;
      @include breakpoint(phone-s){
        max-height: 6vh;
      }
      @include breakpoint(phone-l){
        max-height: 7vh;
      }
  }


// Route Animation
    .slideU-enter-from, .slideU-leave-to {
        transform: scale(0) translateY(10px);
        opacity: 0;
    }
    .slideU-enter-active, .slideU-leave-active {
        transition: all .3s ease-out;
    }
