<template>
    <div>
        <!-- Computer -->
        <div v-if="!isMobile()" class="contacts-container">
            <div @mouseover="$emit('hover-contact', contact.id)" @mouseleave="$emit('unhover-contact', contact.id)" @click="CopyUrl(contact.link)" :key="contact.id" v-for="contact in contacts">
                <div :class="{'hovered':contact.hover===true}">
                    <img :src="require(`@/assets/contacts/${contact.image}`)" :alt='contact.title'>
                    <h3><i :class="contact.logo"></i>  {{contact.title}}</h3>
                    <transition name="fade">
                        <div class="contact-link" ref="url" v-if="contact.hover===true">{{contact.link}}</div>
                    </transition>
                </div>
            </div>
        </div>

        <!-- Mobile -->
        <div v-else class="mobile-contacts-container">
            <div @click="[(contact.title == 'Email') ? SendMail() : CopyUrl(contact.link)]" :key="contact.id" v-for="contact in contacts">
                <div class="mobile-contact">
                    <h3><i :class="contact.logo"></i>  {{contact.title}}</h3>
                    <img :src="require(`@/assets/contacts/${contact.image}`)" :alt='contact.title'>
                </div>
            </div>
        </div>
    </div>
    
</template>

<script>
export default {
    name: 'ContactsExpanded',
    props: {
        contacts: Array,
    },
    emits: ['hover-contact', 'unhover-contact'],
    methods: {
        isMobile() {
            if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                return true
            } else {
                return false
            }
        },
        CopyUrl(url) {
            var regexp =  /^(?:(?:https?|ftp):\/\/)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:\/\S*)?$/;
            navigator.clipboard.writeText(url)
            alert("'"+url+"'" + " has been copied to your clipboard!")
            if (regexp.test(url)) {
                window.open(url, '_blank')
            }
            else if (url.includes('+')){
                console.log('phone')
                var phoneNum = '01083582584'
                var text = 'Hello, please send me a message!'
                text = text.split(' ').join('%20')
                var URL = 'https://api.whatsapp.com/send?phone=82'+phoneNum+'&text='+text
                window.open(URL, 'blank')
            }
        },
        SendMail() {
            document.location.href = "mailto:andrew.taehon.kim@gmail.com?subject=Email%20From%20Website:&body=Hello"
            navigator.clipboard.writeText(url)
            alert("'"+url+"'" + " has been copied to your clipboard!")
        }
    }
}
</script>

