<template>
<!-- Computer -->
    <div v-if="!isMobile()" class="career-container" @mouseover="gifOn()" @mouseleave="gifOff()">
        <div :style="{'color':career.color, 'border-color':career.color}" class="career-title">
            <h1>{{career.job}}</h1>
        </div>
        <div class="image-container">
            <img class="career-image" :src="require(`@/assets/${career.image}`)" :alt='career.job'>
            <img v-if="gif==true" class="career-gif" :src="require(`@/assets/${career.imageG}`)" :alt='career.job'>
        </div>
        <h3 :class="[(gif==true) ? 'blurb-hover' : 'blurb']">{{career.blurb}}</h3>
    </div>
    
<!-- Mobile -->
    <div v-else class="mobile-career-container">
        <div :style="{'color':career.color, 'border-color':career.color}" class="mobile-career-title">
            <h1>{{career.job}}</h1>
        </div>
        <div class="mobile-image-container">
            <img class="mobile-career-image" :src="require(`@/assets/${career.imageG}`)" :alt='career.job'>
        </div>
        <h3 class="mobile-blurb">{{career.blurb}}</h3>
    </div>
</template>

<script>

export default {
    name: 'Career',
    props: {
        career: Object,
    },
    data() {
        return {
            gif: false
        };
    },
    methods: {
        isMobile() {
            if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                return true
            } else {
                return false
            }
        },
        gifOn() {
            this.gif = true;
        },
        gifOff() {
            this.gif = false;
        },
    }
}
</script>

