import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import CareerOpen from '../views/CareerOpen.vue'
import AboutOpen from '../views/AboutOpen.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/profession/:careerName',
    name: 'Career',
    props: true,
    component: CareerOpen
  },
  {
    path: '/:aboutName',
    name: 'About',
    props: true,
    component: AboutOpen
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
