<template>
<!-- Computer -->
    <div v-if="!isMobile()" class="career-container">
        <div class="projects-container">
            <div class="projects">
                <img class="projects-image" :style="{'border-color':career.color}" :src="require(`@/assets/work/computer/${workImage}`)" :alt='career.job'>
                <button :style="{'background-color':career.color}"> More Projects </button>
            </div>
            <div class="career-explanation">
                <h3 :style="{'border-color':career.color}">Mission Statement</h3>
                <h4>{{career.missionStatement}}</h4>
                <h3 :style="{'border-color':career.color}">Recent Work</h3>
                <ul>
                    <li class="recentWork" :key="work" v-for="work in career.recentWork" :style="{'color':career.color}" @mouseover="HoverWork(work[2])" @mouseleave="UnhoverWork(career.recentWork[0][2])" >
                        <h4><a :href="`${work[3]}`" target="_blank" rel="noopener">{{work[0]}}</a> <i class="fas fa-arrow-right" :style="{'color':career.color}"></i> {{work[1]}}</h4>
                    </li>
                </ul>
                <h3 :style="{'border-color':career.color}">Interests</h3>
                <div class="interests">
                    <div class="interest" :key="interest" v-for="interest in career.interests">
                        <i class="far fa-star" :style="{'color':career.color}"></i><h4>{{interest}}</h4>  
                    </div>
                </div>
            </div>
        </div>
    </div>

<!-- Mobile -->
    <div v-else class="mobile-career-container">
            <div class="mobile-projects-container">
                <div class="mobile-projects" >
                    <div class="mobile-image-container"  :style="{'border-color':career.color}">
                        <a :href="`${workURL}`" target="_blank" rel="noopener">
                            <h4>{{workCaption}} <i class="fas fa-arrow-right" :style="{'color':career.color}"></i> {{workDate}}</h4>
                            <img class="mobile-projects-image" :src="require(`@/assets/work/mobile/${workImage}`)" :alt='career.job'>
                        </a>
                        <div class="mobile-carousel-nav">
                            <i class="fas fa-less-than" :style="{'color':career.color}" @click="CarouselNav('left')"></i>
                            <button :style="{'background-color':career.color}"> More Projects </button>
                            <i class="fas fa-greater-than" :style="{'color':career.color}" @click="CarouselNav('right')"></i>
                        </div>
                    </div>
                    <div class="mobile-mission" :style="{'border-color':career.color}">
                        <h3 :style="{'border-color':career.color}">Mission Statement</h3>
                        <h4>{{career.missionStatement}} </h4>
                    </div>
                    <h3 :style="{'border-color':career.color}">Interests</h3>
                    <div class="mobile-interests">
                        <div class="mobile-interest" :key="interest" v-for="interest in career.interests">
                            <i class="far fa-star" :style="{'color':career.color}"></i><h4>{{interest}}</h4>  
                        </div>
                    </div>
                </div>
            </div>
        </div>
</template>

<script>
export default {
    name: 'CareerExpanded',
    props: {
        career: Object,
    },
    data() {
        return {
            workCaption: this.career.recentWork[0][0],
            workImage: this.career.recentWork[0][2],
            workDate: this.career.recentWork[0][1],
            workURL: this.career.recentWork[0][3],
            imageIndex: 0
        }
    },
    methods: {
        isMobile() {
            if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                return true
            } else {
                return false
            }
        },
        HoverWork(image, workCaption){
            this.workImage = image
        },
        UnhoverWork(image, workCaption){
            this.workImage = image
        },
        CarouselNav(direction) {
            if (this.imageIndex == 2 && direction == 'right'){
                this.imageIndex = 0
            }
            else if (this.imageIndex == 0 && direction == 'left') {
                this.imageIndex = 2
            }
            else {
                this.imageIndex = (direction == 'right') ? this.imageIndex+1 : this.imageIndex-1
            }
            this.workCaption = this.career.recentWork[this.imageIndex][0]
            this.workDate = this.career.recentWork[this.imageIndex][1]
            this.workImage = this.career.recentWork[this.imageIndex][2]
            this.workURL = this.career.recentWork[this.imageIndex][3]
        }
    },
}
</script>

