<script>
import AboutHome from './AboutHome.vue'
export default {
  components: { AboutHome },
    name: 'AboutExpanded',
    props: {
        about: Object,
    },
    data() {
        return {
            lieColor: "",
            checkStatement: "Click to Check",
            aboutImage: 'portrait.jpeg'
        }
    },
    methods: {
        isMobile() {
            if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                return true
            } else {
                return false
            }
        },
        CheckLie(id, extra){
            if (id==true){
                alert("Whoops! This is actually true!\n"+extra)
                this.lieColor = "red"
                this.checkStatement = "That was true. Try again!"
            }
            else {
                alert("You caught my lie... :(\n"+extra)
                this.lieColor = "var(--gre)"
                this.checkStatement = "Nice Intuition!"
            }
        },
        lieImage(img) {
            this.aboutImage = img
        }
    },
}
</script>

