  data () {
    return {
      careersData: careersData,
      aboutsData: aboutsData,
      contactsData: contactsData,
      large: 'large'
    }
  },
  methods: {
    isMobile() {
        if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
            return true
        } else {
            return false
        }
    },
  }
